@import "../vars.scss";
.breadcrumbs {
    padding: 1.25em 0 2.5em;
    &__item {
        font-size: .875em;
        line-height: 120%;
        position: relative;
        display: inline;
        color: $gray2;
        &:not(:first-child) {
            margin-left: 2.85em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: 1px;
                height: .65em;
                background-color: #DEE2E9;
                top: .28em;
                right: 100%;
                margin-right: 1.4em;
            }
        }
    }
}

@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }
}
